import { verifyPhone } from '../../../utils/utils.js'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel() 
export default {
	data() {
		return {
			surplusSecond: 0, // 还剩多少秒可以重发
			readySendCode: true,  // 是否准备好发送验证码
			verifyCodeTimer: null, //定时器对象
			focusArray: ['name', 'age'],
			focusIndex: null,  //焦点位置
			agreement:false, // 是否同意用户协议
			name: '',
			age: '',
			phone: '',
			verifyCode: '',
		};
	},
	beforeDestroy() {
		clearInterval(this.verifyCodeTimer)
		this.verifyCodeTimer = null
	},
	components:{
		uniPopup
	},
	mounted() {
		uni.showToast({
			icon: 'none',
			title: '请完善信息'
		})
	},
	methods:{
		agreementChange(){
			this.agreement = !this.agreement
			
		},
		handleBlur(index){
			let focusArray = this.focusArray
			if(index != focusArray.length - 1 ){
				if(!this[focusArray[index + 1]]){
					this.focusIndex = index + 1
					return
				}
				this.focusIndex = null
				return
			}
			this.focusIndex = null	
		},
		submit(){
			if(!this.name){
				uni.showToast({
					icon:'none',
					title:'请输入姓名'
				})
				this.$nextTick(()=>{
					this.focusIndex = 0
				})
				return
			}
			if(!this.age){
				uni.showToast({
					icon:'none',
					title:'请输入年龄'
				})
				this.$nextTick(()=>{
					this.focusIndex = 1
				})
				return
			}
			if(!this.agreement){
				uni.showToast({
					icon:'none',
					title:'必须同意用户协议'
				})
				return
			}
			userModel.register(this.name, this.age).then(res=>{
				// 注册成功
				const { code, message, data } = res.data
				if(code === '0'){
					uni.setStorageSync('isRegister', 0)
					this.$refs.registerSuccess.open()
					// uni.showToast({
					// 	title: '登录成功，即将跳转'
					// })
					setTimeout(()=>{
						uni.reLaunch({
							url: '../../customer/main/main'
						})
					},2000)	
				}else{
					uni.showToast({
						icon:'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title: '提交失败，请稍候重试'
				})
			})
		},
		toLogin(){
			uni.reLaunch({
				url: '../login/login'
			})
		}
	}
}
